import React, {Component} from 'react';// import KanbanBoard from './KanbanBoard';import update from "react-addons-update";//Polyfillsimport 'babel-polyfill';import 'whatwg-fetch';const API_URL = 'http://kanbanapi.pro-react.com';const API_HEADERS = {	'Content-Type': 'application/json',	Authorization: 'Jason'};class KanbanBoardContainer extends Component {	constructor() {		super(...arguments);		this.state = {			cards: [],		};	}	componentDidMount() {		fetch(`${API_URL}/cards`, {headers: API_HEADERS})			.then((response) => response.json())			.then((responseData) => {				console.log("Data loaded!");				this.setState({cards: responseData});			})			.catch((error) => {				console.log("Error fetching and parsing data", error);			});	}	//taskaddTask, deleteTask, toggleTask	//TODO:tasktoggleTasktask	addTask(cardId, taskName) {		//card		let cardIndex = this.state.cards.findIndex((card) => (card.id === cardId));		//nameid,task		let newTask = {id: Date.now(), name: taskName, done: false};		// "newTask" Push		let nextState = update(this.state.cards, {			[cardIndex]: {				tasks: {$push: [newTask]}			}		});		//nextStatecardsstate.		this.setState({cards: nextState});		//API		fetch(`${API_URL}/cards/${cardId}/tasks`, {			method: 'post',			headers: API_HEADERS,			body: JSON.stringify(newTask)		})			.then((response) => response.json())			.then((responseData) => {				newTask.id = responseData.id;				this.setState({cards: nextState});			});		console.log("Task added!")	}	deleteTask(cardId, taskId, taskIndex) {		let cardIndex = this.state.cards.findIndex((card) => (card.id === cardId));		let nextState = update(this.state.cards, {			[cardIndex]: {				tasks: {$splice: [[taskIndex, 1]]}			}		});		this.setState({cards: nextState});		fetch(`${API_URL}/cards/${cardId}/tasks/${taskId}`, {			method: 'delete',			headers: API_HEADERS		});		console.log("Task deleted!")	}	toggleTask(cardId, taskId, taskIndex) {		//		let cardIndex = this.state.cards.findIndex((card) => card.id === cardId);		let newDoneValue;		let nextState = update(this.state.cards, {			[cardIndex]: {				tasks: {					[taskIndex]: {						done: {							$apply: (done) => {								newDoneValue = !done;								return newDoneValue;							}						}					}				}			}		});		this.setState({cards: nextState});		fetch(`${API_URL}/cards/${cardId}/tasks/${taskId}`, {			method: 'put',			headers: API_HEADERS,			body: JSON.stringify({done: newDoneValue})		});		console.log("Task toggled!")	}	//End 	//	updateCardStatus(cardId, listId) {		let cardIndex = this.state.cards.findIndex((card) => card.id === cardId);		let card = this.state.cards[cardIndex];		if (card.status !== listId) {			this.setState(update(this.state, {				cards: {					[cardIndex]: {						status: {$set: listId} //cardlist					}				}			}));		}	}	updateCardPosition(cardId, afterId) {		if (cardId !== afterId) {			// eslint-disable-next-line			let cardIndex = this.state.cards.findIndex((card) => card.id == cardId);			let card = this.state.cards[cardIndex];			// eslint-disable-next-line			let afterIndex = this.state.cards.findIndex((card) => card.id == afterId);			this.setState(update(this.state, {				cards: {					$splice: [						[cardIndex, 1],						[afterIndex, 0, card]					]				}			}));		}	}	//End 	//Card addCardEditCard	addCard(card) {		//state		let prevState = this.state;		//ID		if (card.id === null) {			card = Object.assign({}, card, {id: Date.now()});		}		//Cardcards		let nextState = update(this.state.cards, {$push: [card]});		//cards,		this.setState({cards: nextState});		fetch(`${API_URL}/cards`, {			method: 'post',			headers: API_HEADERS,			body: JSON.stringify(card)		})			.then((response) => {				if (response.ok) {					return response.json()				} else {					//error					throw new Error("Server response wasn't OK")				}			})			.then((responseData) => {				//IDReact				card.id = responseData.id;				this.setState({cards: nextState});			})			.catch((error) => {				//errorstate				this.setState(prevState);			});	}	updateCard(card) {		let prevState = this.state;		//eslint-disable-next-line		let cardIndex = this.state.cards.findIndex((c) => c.id == card.id);		let nextState = update(this.state.cards, {[cardIndex]: {$set: card}});		this.setState({cards: nextState});		fetch(`${API_URL}/cards/${card.id}`, {			method: 'put',			headers: API_HEADERS,			body: JSON.stringify(card)		})			.then((response) => {				if (!response.ok) {					//error					throw new Error("Server response wasn't OK")				}else{					console.log("card updated!")				}			})			.catch((error) => {				//error				console.error("Fetch error:", error);				this.setState(prevState);			});	}	//TODO:deleteCard()	render() {		let kanbanBoard = this.props.children && React.cloneElement(			this.props.children, {				cards: this.state.cards,				taskCallbacks: {					toggle: this.toggleTask.bind(this),					delete: this.deleteTask.bind(this),					add: this.addTask.bind(this)				},				cardCallbacks: {					addCard: this.addCard.bind(this),					updateCard: this.updateCard.bind(this),					updateStatus: this.updateCardStatus.bind(this),					updatePosition: this.updateCardPosition.bind(this),				}			}		);		return kanbanBoard;	}}export default KanbanBoardContainer;


